import React from 'react';
import $ from "jquery";


function Close() {
    
    const close = () => {
        $('.displaymenu').slideUp(500).hide(500);
        $('.displaymenutext').slideUp(500).hide(500);
        $('.menublock').hide(500);
        $('.contactblock').hide(500);
        document.getElementsByClassName('close')[0].style.display = "none";
    }

    return (
        <span className="close" onClick={close}>&#8592;</span>
    ) 
}

export default Close